body {
  color: #333;
  background-color: #eef2f7;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px 30px;
  box-shadow: 0 2px 8px #0000001a;
}

h1, h2 {
  text-align: center;
  color: #222;
  margin-bottom: 20px;
}

#get-students-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 6px;
  margin: 0 auto 25px;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

#get-students-btn:hover {
  background-color: #0056b3;
}

#students-table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 30px;
}

#students-table th, #students-table td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 10px;
}

#students-table th {
  background-color: #f7f9fc;
  font-weight: bold;
}

#students-table tbody tr:nth-child(2n) {
  background-color: #f9fafd;
}

#students-table button.update-btn, #students-table button.delete-btn {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 0 3px;
  padding: 5px 12px;
  font-size: 14px;
}

#students-table button.update-btn {
  color: #fff;
  background-color: #17a2b8;
}

#students-table button.update-btn:hover {
  background-color: #117a8b;
}

#students-table button.delete-btn {
  color: #fff;
  background-color: #dc3545;
}

#students-table button.delete-btn:hover {
  background-color: #a71d2a;
}

form label {
  margin-bottom: 10px;
  font-weight: 600;
  display: block;
}

form input[type="text"], form input[type="number"], form input[type="email"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 15px;
  padding: 8px 10px;
  font-size: 15px;
}

form input[type="checkbox"] {
  vertical-align: middle;
  cursor: pointer;
  margin-left: 10px;
  transform: scale(1.1);
}

form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: 10px 20px;
  font-size: 16px;
}

form button[type="submit"]:hover {
  background-color: #1e7e34;
}
